<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
                <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                <div style="height: 150px; background-size: cover; background-position: center"
                :style="{backgroundImage:`url(${item.image})`}">
                </div>
                <div class="card-body">
                <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
                <h5 class="card-title">
                    <a href="#" class="text-dark">{{item.title}}</a>
                </h5>
                <p class="card-text">{{item.content}}</p>
                <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h5" v-if="!item.price">{{item.origin_price}}</div>
                    <del class="h6" v-if="item.price">{{item.origin_price}}</del>
                    <div class="h5" v-if="item.price">{{item.price}}</div>
                </div>
                </div>
                <div class="card-footer d-flex">
                <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
                    <i class="fas fa-spinner fa-spin"></i>
                    查看更多
                </button>
                <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                    <i class="fas fa-spinner fa-spin"></i>
                    加到購物車
                </button>
                </div>
                </div>
            </div>
        </div> 
            <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
                aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                    </div>
                    <div class="modal-body">
                        <div class="row">
                        <div class="col-sm-4">
                            <img :src=" product.image"
                            class="img-fluid" alt="">
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                            <label for="title">{{product.title}}</label>
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category">{{product.category}}</label>
                            </div>
                            </div>
                            <div class="form-row">
                            <div class="form-group col-md-6">
                            <label for="origin_price">原價:{{product.origin_price}}</label>
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">特價:{{product.price}}</label>
                            </div>
                            </div>
                            <hr>
                            <div class="form-group">
                            <label for="description">{{product.description}}</label>
                            </div>
                            <div class="form-group">
                            <label for="content">{{product.content}}</label>
                            </div>
                            <div class="form-group">
                                <div class="d-flex justify-content-between align-items-baseline">
                                </div>
                                <select name="" class="from-control mt-3" v-model="product.num">
                                    <option :value="num" v-for="num in 10" :key="num" >
                                        選購{{num}} {{product.unit}}
                                    </option>
                                </select>    
                            </div>
                        </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            小計 <strong>{{product.num * product.price}}</strong>元
                        </div>
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="addtoCart(product.id,product.num)">加入購物車</button>
                    </div>
                    </div>
                </div>
                </div>
                <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
                aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div class="modal-dialog" role="document">
                        <div class="modal-content border-0">
                            <div class="modal-header bg-danger text-white">
                            </div>
                        </div>
                    </div>
                </div>
                <div class="container  mt-4">
                    
                    <table class="table">
                        <thead>
                            <th></th>
                            <th>品名</th>
                            <th>數量</th>
                            <th>單價</th>
                        </thead>
                        <tbody>
                            <tr v-for="item in cart.carts" :key="item.id">
                            <td class="align-middle">
                                <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCatItem(item.id)">
                                <span class="far fa-trash-alt">刪除</span>
                                </button>
                            </td>
                            <td class="align-middle">
                                {{ item.product.title }}
                                <!-- <div class="text-success" v-if="item.coupon">
                                已套用優惠券
                                </div> -->
                            </td>
                            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                            <td class="align-middle text-right">{{ item.final_total }}</td>
                            </tr>
                        </tbody>
                        <tfoot>
                            <tr>
                            <td colspan="3" class="text-right">總計</td>
                            <td class="text-right">{{ cart.total }}</td>
                            </tr>
                            <tr v-if="cart.final_total !== cart.total">
                            <td colspan="3" class="text-right text-success">折扣價</td>
                            <td class="text-right text-success">{{ cart.final_total }}</td>
                            </tr>
                        </tfoot>
                        </table>
                        <div class="input-group mb-3 input-group-sm">
                        <input type="text" class="form-control" placeholder="請輸入優惠碼">
                        <div class="input-group-append">
                            <button class="btn btn-outline-secondary" type="button">
                            套用優惠碼
                            </button>
                        </div>
                    </div>
                </div>



                <div class="my-5 row justify-content-center">
                        <form class="col-md-6" @submit.prevent="createOrder">
                            <div class="form-group">
                            <label for="useremail">Email</label>
                            <input type="email" class="form-control" name="email" id="useremail"
                                v-model="form.user.email" placeholder="請輸入 Email" required>
                            <span class="text-danger"></span>
                            </div>
                        
                            <div class="form-group">
                            <label for="username">收件人姓名</label>
                            <input type="text" class="form-control" name="name" id="username"
                                v-model="form.user.name" placeholder="輸入姓名" >
                            <span class="text-danger"></span>
                            </div>
                        
                            <div class="form-group">
                            <label for="usertel">收件人電話</label>
                            <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
                            </div>
                        
                            <div class="form-group">
                            <label for="useraddress">收件人地址</label>
                            <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                                placeholder="請輸入地址">
                            <span class="text-danger">地址欄位不得留空</span>
                            </div>
                        
                            <div class="form-group">
                            <label for="comment">留言</label>
                            <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                            </div>
                            <div class="text-right">
                            <button class="btn btn-danger">送出訂單</button>
                            </div>
                        </form>
                </div>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    data(){
        return{
            products:[],
            product:{},
            tempProduct:{},
            isNew:false,
            isLoading: false,
            cart:{},
            form:{
                user:{
                    name:'',
                    email:'',
                    tel:'',
                    address:'',
                },
                message:'',
            },

        };
    },
    methods:{
        getProducts(){
                const vm = this;
                const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/products?page=:page`;
                vm.isLoading = true ;
                this.$http.get(url).then((response) => {
                console.log(response.data);
                vm.products = response.data.products ;
                vm.isLoading = false ;
                });
                },
        getProduct(id){
                        const vm = this;
                        const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/product/${id}`;
                        vm.isLoading = true ;
                        this.$http.get(url).then((response) => {
                        vm.product = response.data.product;
                        $('#productModal').modal('show');
                        console.log(response);
                        vm.isLoading = false ;
                        });
                },
        addtoCart(id,qty = 1){
                        const vm = this;
                        const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/cart`;
                        vm.isLoading = true ;
                        const cart = {
                            product_id:id,
                            qty
                        }
                        this.$http.post(url,{data:cart}).then((response) => {
                        console.log(response);
                        vm.isLoading = false ;
                        vm.getCart();
                        $('#productModal').modal('hide');
                        });
                },
        getCart(){
                const vm = this;
                const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/cart`;
                vm.isLoading = true ;
                this.$http.get(url).then((response) => {
                console.log(response.data);
                vm.cart = response.data.data;
                vm.isLoading = false ;
                });
        },
        removeCatItem(id){
                const vm = this;
                const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/cart/${id}`;
                vm.isLoading = true ;
                this.$http.delete(url).then(() => {
                vm.getCart();
                vm.isLoading = false ;
                });
        },
        createOrder(){
                const vm = this;
                const url = `${process.env.APIPATN}/api/${process.env.CUSTOMPATH}/order`;
                const order = vm.form;
                vm.isLoading = true ;
                this.$http.post(url,{data:order}).then((response) => {
                console.log('建立訂單',response);
                if(response.data.success){
                    vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                }
                vm.isLoading = false ;
                });
        },
    },
    created(){
            this.getProducts();
            this.getCart();
        }
};
</script> 


